'use strict';

angular.module('adama-web').directive('layoutFix', ["$rootScope", function($rootScope) {
	return {
		scope: {
			addEvent: '='
		},
		restrict: 'E',
		link: function postLink(scope) {
			if (scope.addEvent) {
				$rootScope.$on('$viewContentLoaded', function() {
					$.AdminLTE.layout.fix();
				});
			}
			$.AdminLTE.layout.fix();
		}
	};
}]);
